
#navBar {
    color: white;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 20;
    position: sticky;

}

.nav  {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    padding: 1rem;
}

.navTitle {
    width: 25%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: white;
}

.navTitle h1 {
    margin: 0;
    color: white;
}

.navItems {
    width: 75%;
    display: flex;
    justify-content: flex-end;
}

.navItems a {
    color: white;
    font-size: 1.1rem;
    padding: 12.5px;

    text-shadow: 1px 1px 5px black;
    font-family: 'Nanum Gothic', sans-serif;
}

.navItems a:hover {
    opacity: 0.75;
}

.navItems button {
    display: block;
    background: transparent;
    border: 0;
    color: white;
    font-size: 1.1rem;
    padding: 12.5px;

    cursor: pointer;

    text-shadow: 1px 1px 5px black;
    font-family: 'Nanum Gothic', sans-serif;
}

.navItems button:hover {
    opacity: 0.75;
}

#navBar span {
    color: orange;
}

.bottom {
    text-align: end;
}