#logIn {
    display: none;
    width: 100vw;
    height: 100vh;
    justify-content: center;
    align-items: center;
    font-family: 'Encode Sans SC', sans-serif;
}

#logIn .regMain {
    display: flex;
    flex-direction: column;
    align-items: center;
    border: 1px solid lightcyan;
    border-radius: 15px;
    background: aliceblue;
    width: min-content;
}

#logIn .logButtons {
    width: 100%;
    display: flex;
    justify-content: center;
    background: aliceblue;
    border-bottom: 3px solid white;
}

#logIn #errorLogin {
    margin: 1rem;
    border-radius: 15px;
    background-color: white;
    padding: 0px 5px 0px 5px;
    display: none;
    
}

#logIn #errorLogin p {
    font-size: 15px;
    word-wrap: break-word;
    color: red;
}

#logIn #errorRegister {
    margin: 1rem;
    border-radius: 15px;
    background-color: white;
    padding: 0px 5px 0px 5px;
    display: none;
    
}

#logIn #errorRegister p {
    font-size: 15px;
    word-wrap: break-word;
    color: red;
}

#logIn #loginButton {
    height: 40px;
    width: 50%;
    border: transparent;
    border-radius: 5px 25px 5px 5px;
    cursor: pointer;
    box-shadow: 1px 1px 5px antiquewhite;
    background-color: white;
}

#logIn #registerButton {
    height: 40px;
    width: 50%;
    border: transparent;
    border-radius: 25px 5px 5px;
    cursor: pointer;
    box-shadow: 1px 1px 5px antiquewhite;
    background-color: white;
}

#loginButton:hover, #registerButton:hover, #logIn form button:hover {
    background-color: gray !important;
}

#logIn arc {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    height: 100%;
    width: 25vw;
    padding: 1rem;
}

#logIn arc button {
    height: 35px;
    background: white;
    border: none;
    box-shadow: 1px 1px 5px antiquewhite;
}

#logIn arc button:hover {
    background: lightgray;
}

#logIn input {
    margin: 1rem;
    height: 35px;
    background: transparent;
    border: none;
    color: black;
    border-bottom: 3px solid white;
}